import * as C from '../types';

export const ProjectAll = (state = { isFetching: true }, action) => {
	switch (action.type) {
		case C.FETCH_PROJECT_ALL:
			return { ...state, isFetching: true };
		case C.FINISHED_FETCH_PROJECT_ALL:
			return { ...state, isFetching: false, ...action.data };
		case C.ERROR_FETCH_PROJECT_ALL:
			return {
				...state,
				isFetching: false,
				error: action.error,
			};
		default:
			return state;
	}
};
